___CSS_LOADER_EXPORT___.push([module.id, `.skeleton {
  position: relative;
  margin-bottom: .25rem;
}

.skeleton::after{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  z-index: 5;
  animation: skeleton-loading 1s linear infinite alternate;
  background-size: 80%;
  border-radius: 8px;
}

.skeleton-text {
  display: inline-block;
  height: max-content;
  width: 80%;
  font-size: .8em;
  background-size: 40%;
  margin-bottom: .3rem;
}

.skeleton-text:last-child {
  width: 70%;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }
  
  100% {
    background-color: hsl(200, 20%, 100%);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/skeleton.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,SAAS;EACT,QAAQ;EACR,WAAW;EACX,UAAU;EACV,wDAAwD;EACxD,oBAAoB;EACpB,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,mBAAmB;EACnB,UAAU;EACV,eAAe;EACf,oBAAoB;EACpB,oBAAoB;AACtB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE;IACE,oCAAoC;EACtC;;EAEA;IACE,qCAAqC;EACvC;AACF","sourcesContent":[".skeleton {\r\n  position: relative;\r\n  margin-bottom: .25rem;\r\n}\r\n\r\n.skeleton::after{\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  bottom: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  z-index: 5;\r\n  animation: skeleton-loading 1s linear infinite alternate;\r\n  background-size: 80%;\r\n  border-radius: 8px;\r\n}\r\n\r\n.skeleton-text {\r\n  display: inline-block;\r\n  height: max-content;\r\n  width: 80%;\r\n  font-size: .8em;\r\n  background-size: 40%;\r\n  margin-bottom: .3rem;\r\n}\r\n\r\n.skeleton-text:last-child {\r\n  width: 70%;\r\n}\r\n\r\n@keyframes skeleton-loading {\r\n  0% {\r\n    background-color: hsl(200, 20%, 70%);\r\n  }\r\n  \r\n  100% {\r\n    background-color: hsl(200, 20%, 100%);\r\n  }\r\n}"],"sourceRoot":""}]);
