___CSS_LOADER_EXPORT___.push([module.id, `#details {
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 2rem;
  box-sizing: border-box;
}

#details .details__container{
  width: 88%;
}

#details .details__container .restaurant__banner{
  width: 100%;
  border-radius: 4px;
  box-sizing: border-box;
}

#details .details__container .restaurant__information{
  border-bottom: 1px solid black;
  padding: 0 0 2rem;
  margin: 0 0 1rem;
}

#details .details__container .restaurant__information .restaurant__status{
  display: flex;
  justify-content: space-between;
  padding: 0 .2rem;

  font-size: .9em;
}

#details .details__container .restaurant__information .restaurant__title-area{
  display: flex;
  align-items: end;
  margin: 0 0 1rem;
}

#details .details__container .restaurant__information .restaurant__title-area .restaurant__title{
  font-size: 2.5em;
  font-weight: 300;
}

#details .details__container .restaurant__information .restaurant__title-area .restaurant__categories{
  padding: 0 .5rem .6rem;
  font-size: .9em;
  opacity: 60%;
}

#details .details__container .restaurant__information .restaurant__description{
  padding: 0 .25rem 2rem;
  margin: 0 0 1rem;
  box-sizing: border-box;
  border-bottom: 1px solid black;
}

#details .details__container .restaurant__information .restaurant__menu{
  display: flex;
  justify-content: space-between;
}

#details .details__container .restaurant__information .restaurant__menu .restaurant__menu__items{
  width: 100%;
  padding: 0 1rem;
}

#details .details__container .restaurant__information .restaurant__menu .restaurant__menu__items h3{
  font-size: 1.8em;
  font-weight: 400;
}

#details .details__container .restaurant__information .restaurant__menu .restaurant__menu__items ul li{
  list-style: disc;
  margin: 0 .7rem;
  opacity: 90%;
}

#details .details__container #review{
  padding: 0 1rem;
  box-sizing: border-box;
}

#details .details__container #review h3{
  font-size: 2em;
  margin: 1rem 0;
}

#details .details__container .restaurant__review{
  display: flex;
  padding: 1rem;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.199);
}

#details .details__container .restaurant__review .review__profile{
  height: 80px;
  width: 80px;
  margin: 0 1rem;
}

#details .details__container .restaurant__review .review__detail{
  display: flex;
}

#details .details__container .restaurant__review .review__detail .review__name{
  font-weight: 600;
}

#details .details__container .restaurant__review .review__detail .review__date{
  padding: 0 .5rem;
  font-weight: 300;
}

#likeButtonContainer .like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #a32121;
  
  position: fixed;
  bottom: 20px;
  right: 20px;
  border-radius: 15px;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
