___CSS_LOADER_EXPORT___.push([module.id, `header {
    width: 100%;
    position: absolute;
    z-index: 9999;
}

.navbar-fixed {
    position: fixed;
    background-color: rgba(23, 23, 23, 0.479);
    box-shadow: inset 0 -1px 1px rgb(23, 23, 23);
    height: 45px;
}

.skip-content{
    position: absolute;
    top: -100%;
    text-decoration: none;
    color: var(--fouth);
    background-color: var(--third);
    padding: .5rem .25rem .75rem;
    border-radius: 0 0 10px 10px;

    transition: ease-in-out 200ms;
}

.skip-content:focus{
    top: 0;
}

header.navbar-fixed h2{
    transform: scaleY(0);
    opacity: 0;
}

header.navbar-fixed nav{
    transform: translateY(-5rem);
}

header h2{
    text-align: center;
    color: var(--fouth);
    padding: 1rem 0 .2rem;
    font-size: 2em;
    transform-origin: top;

    transition: ease-in-out 200ms;
}

header h2 span{
    color: var(--third);
}

nav {
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-1rem);
    cursor: pointer;
    padding: .25rem 0;
    box-sizing: border-box;
    position: relative;

    transition: ease-in-out 200ms;
}

.navbar-menu ul li{
    margin: 10px;
    display: inline-block;
    text-align: center;
}

.navbar-menu a{
    display: inline-block;
    position: relative;
    color: var(--fouth);
    transition: ease-in-out 200ms;
    font-size: 1.2em;
    padding: .5rem 0;
}

.navbar-menu ul li a:hover {
    transform: scale(1.1);
    color: var(--third);
}

nav .navbar-logo {
    position: relative;
    width: 44px;
    height: 44px;
    display: none;
    margin: .4rem 0 0;

    background-color: transparent;
    border: none;
}

header.navbar-fixed .navbar-logo i{
    color: white;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
}

header.navbar-fixed .navbar-logo:hover i{
    color: var(--third);
}

nav .navbar-logo .open-logo,
nav .navbar-logo .close-logo {
    color: var(--fourth-color);
    position: absolute;
    font-size: 1.4em;
    bottom: 0;
    left: 0;

    padding: 1.3rem 0 0;

    transition: 300ms ease-in-out;
}`, "",{"version":3,"sources":["webpack://./src/styles/nav.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,yCAAyC;IACzC,4CAA4C;IAC5C,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,qBAAqB;IACrB,mBAAmB;IACnB,8BAA8B;IAC9B,4BAA4B;IAC5B,4BAA4B;;IAE5B,6BAA6B;AACjC;;AAEA;IACI,MAAM;AACV;;AAEA;IACI,oBAAoB;IACpB,UAAU;AACd;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;IACnB,qBAAqB;IACrB,cAAc;IACd,qBAAqB;;IAErB,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,4BAA4B;IAC5B,eAAe;IACf,iBAAiB;IACjB,sBAAsB;IACtB,kBAAkB;;IAElB,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,kBAAkB;IAClB,mBAAmB;IACnB,6BAA6B;IAC7B,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,iBAAiB;;IAEjB,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,WAAW;IACX,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;;IAEI,0BAA0B;IAC1B,kBAAkB;IAClB,gBAAgB;IAChB,SAAS;IACT,OAAO;;IAEP,mBAAmB;;IAEnB,6BAA6B;AACjC","sourcesContent":["header {\r\n    width: 100%;\r\n    position: absolute;\r\n    z-index: 9999;\r\n}\r\n\r\n.navbar-fixed {\r\n    position: fixed;\r\n    background-color: rgba(23, 23, 23, 0.479);\r\n    box-shadow: inset 0 -1px 1px rgb(23, 23, 23);\r\n    height: 45px;\r\n}\r\n\r\n.skip-content{\r\n    position: absolute;\r\n    top: -100%;\r\n    text-decoration: none;\r\n    color: var(--fouth);\r\n    background-color: var(--third);\r\n    padding: .5rem .25rem .75rem;\r\n    border-radius: 0 0 10px 10px;\r\n\r\n    transition: ease-in-out 200ms;\r\n}\r\n\r\n.skip-content:focus{\r\n    top: 0;\r\n}\r\n\r\nheader.navbar-fixed h2{\r\n    transform: scaleY(0);\r\n    opacity: 0;\r\n}\r\n\r\nheader.navbar-fixed nav{\r\n    transform: translateY(-5rem);\r\n}\r\n\r\nheader h2{\r\n    text-align: center;\r\n    color: var(--fouth);\r\n    padding: 1rem 0 .2rem;\r\n    font-size: 2em;\r\n    transform-origin: top;\r\n\r\n    tran