___CSS_LOADER_EXPORT___.push([module.id, `#about{
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#about .about__container{
    display: flex;
    justify-content: center;

    width: 57%;
}

#about .about__container .about__banner{
    width: 30rem;
    height: 14rem;

    box-shadow: 0 0 5px black;
    border-radius: 6px;
    overflow: hidden;
}

#about .about__container .about__banner img{
    height: 100%;
    width: 100%;

    object-fit: cover;
    object-position: center;
    filter: blur(.5px);

    transition: ease-in-out 300ms;
}

#about .about__container .about__banner:hover img{
    transform: scale(1.2);
    filter: blur(0);
}

#about .about__container #about__content{
    height: 100%;
    width: 100%;
    padding: .3rem 0 0 1rem;
}

#about .about__container .about__title{
    font-size: 2em;
    padding: 0 0 .5em;
}`, "",{"version":3,"sources":["webpack://./src/styles/about.css"],"names":[],"mappings":"AAEA;IACI,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;;IAEvB,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,aAAa;;IAEb,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,WAAW;;IAEX,iBAAiB;IACjB,uBAAuB;IACvB,kBAAkB;;IAElB,6BAA6B;AACjC;;AAEA;IACI,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,uBAAuB;AAC3B;;AAEA;IACI,cAAc;IACd,iBAAiB;AACrB","sourcesContent":["@import url(./root.css);\r\n\r\n#about{\r\n    height: 50vh;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n#about .about__container{\r\n    display: flex;\r\n    justify-content: center;\r\n\r\n    width: 57%;\r\n}\r\n\r\n#about .about__container .about__banner{\r\n    width: 30rem;\r\n    height: 14rem;\r\n\r\n    box-shadow: 0 0 5px black;\r\n    border-radius: 6px;\r\n    overflow: hidden;\r\n}\r\n\r\n#about .about__container .about__banner img{\r\n    height: 100%;\r\n    width: 100%;\r\n\r\n    object-fit: cover;\r\n    object-position: center;\r\n    filter: blur(.5px);\r\n\r\n    transition: ease-in-out 300ms;\r\n}\r\n\r\n#about .about__container .about__banner:hover img{\r\n    transform: scale(1.2);\r\n    filter: blur(0);\r\n}\r\n\r\n#about .about__container #about__content{\r\n    height: 100%;\r\n    width: 100%;\r\n    padding: .3rem 0 0 1rem;\r\n}\r\n\r\n#about .about__container .about__title{\r\n    font-size: 2em;\r\n    padding: 0 0 .5em;\r\n}"],"sourceRoot":""}]);
